.dateDropdown {
	margin: 0;
	padding: 0;

	max-width: 320px;

	display: flex;
	// flex-wrap: wrap;
	justify-content: space-between;

	list-style: none;
    text-decoration: none;
}

.dateDropdown__element {
	margin: 0;
	padding: 0;

	min-width: 130px;
	width: 100%;
	max-width: 150px;

	list-style: none;
    text-decoration: none;
}

.dateDropdown__headline {
	margin: 0;
	padding: 0 0 6px 0;

	font-family: Montserrat;
	font-style: normal;
	text-align: left;
	font-size: 12px;
	line-height: 15px;
	font-weight: 700;
	text-transform: uppercase;
	color: opacify($DarkShade, 1);
}

.dateDropdown__content {
	margin: 0;
	padding: 0 0 4px 13px;

	display: flex;

	min-width: 130px;
	width: 100%;
	max-width: 150px;
	height: 44px;
	
	position: relative;
	outline: none;
	opacity: 1;

	justify-content: left;
	align-items: center;
	
	font-family: Montserrat;
	font-style: normal;
	text-align: left;
	font-size: 14px;
	line-height: 18px;
	font-weight: 400;
	// text-transform: uppercase;
	color: opacify($DarkShade, 0.75);

	border: 1px solid opacify($DarkShade, 0.25);
	box-sizing: border-box;
	border-radius: 4px;
}

