.dateDropdown__content:hover {
	border: 1px solid opacify($DarkShade, 0.5);
	box-sizing: border-box;
	border-radius: 4px;
}

.dateDropdown__content::before {
	content: '';
	margin: 0;
	padding: 0;

	position: absolute;
	top: 14px;
	right: 16px;

	width: 7px;
	height: 7px;

	border: solid opacify($DarkShade, 0.5);
	border-width: 0 2px 2px 0;
	display: inline-block;
	// padding: 4px;

	transform: rotate(45deg);
	-webkit-transform: rotate(45deg);
}